import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import { BootstrapVue, IconsPlugin } from "bootstrap-vue";
//terminal pro icons1: npm install --save @fortawesome/fontawesome-svg-core @fortawesome/free-solid-svg-icons
//terminal pro icons2: npm install --save @fortawesome/vue-fontawesome
import { library } from "@fortawesome/fontawesome-svg-core";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";
import {
  faHospitalSymbol,
  faShieldAlt,
  faUmbrellaBeach,
  faCamera,
  faUtensils,
  faBed,
  faRestroom,
  faArrowDown,
  faHome,
  faHandsHelping,
  faGlassCheers
} from "@fortawesome/free-solid-svg-icons";

Vue.config.productionTip = false;

// Install BootstrapVue
Vue.use(BootstrapVue);
// Optionally install the BootstrapVue icon components plugin
Vue.use(IconsPlugin);

library.add([
  faShieldAlt,
  faHospitalSymbol,
  faUmbrellaBeach,
  faCamera,
  faUtensils,
  faBed,
  faRestroom,
  faArrowDown,
  faHome,
  faHandsHelping,
  faGlassCheers
]);

Vue.component("font-awesome-icon", FontAwesomeIcon);

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
