import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Delegacias from "../views/Delegacias.vue";
import PontosTuristicos from "../views/PontosTuristicos.vue";
import OndeComer from "../views/OndeComer.vue";
import OndeDormir from "../views/OndeDormir.vue";
import Eventos from "../views/Eventos.vue";
import Banheiros from "../views/Banheiros.vue";
import Hospitais from "../views/Hospitais.vue";
import DetailToilets from "../components/DetailToilets.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/praias",
    name: "Praias",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/Praias.vue")
  },
  {
    path: "/delegacias",
    name: "Delegacias",
    component: Delegacias
  },
  {
    path: "/hospitais",
    name: "Hospitais",
    component: Hospitais
  },
  {
    path: "/pontosturisticos",
    name: "PontosTuristicos",
    component: PontosTuristicos
  },
  {
    path: "/ondecomer",
    name: "OndeComer",
    component: OndeComer
  },
  {
    path: "/ondedormir",
    name: "OndeDormir",
    component: OndeDormir
  },
  {
    path: "/eventos",
    name: "Eventos",
    component: Eventos
  },
  {
    path: "/banheiros",
    name: "Banheiros",
    component: Banheiros
  },

  {
    path: "/banheiros/:id",
    name: "DetailToilets",
    component: DetailToilets
  }
];

const router = new VueRouter({
  mode: "history",
  routes
});

export default router;
